@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.fade-in {
  opacity: 0;
  animation: fadeIn ease 1;
  animation-duration: 1s;
  animation-fill-mode: forwards;

  &.one {
    animation-delay: .8s;
  }

  &.two {
    animation-delay: 2.3s;
  }

  &.three {
    animation-delay: 3.5s;
  }
}
