.parallax {
  position: absolute;
  width: 100%;
  height: 100%;
  flex: 1 0 100%;
  overflow: hidden;

  &__el {
    position: absolute;
  }

  &__el span {
    display: inline-block;
  }

  &__el--1 {
    top: 7% !important;
    left: 23% !important;
  }

  &__el--1 span {
    width: 53px;
    height: 55px;
    background: url('../img/grain1.png') 0 0 / 100% no-repeat;
  }

  &__el--2 {
    top: 65% !important;
    left: 22% !important;
  }

  &__el--2 span {
    width: 49px;
    height: 50px;
    background: url('../img/grain2.png') 0 0 / 100% no-repeat;
  }

  &__el--3 {
    top: 37% !important;
    left: 45% !important;
  }

  &__el--3 span {
    width: 102px;
    height: 108px;
    background: url('../img/grain3.png') 0 0 / 100% no-repeat;
  }

  &__el--4 {
    top: 27% !important;
    left: 66% !important;
  }

  &__el--4 span {
    width: 32px;
    height: 34px;
    background: url('../img/grain4.png') 0 0 / 100% no-repeat;
  }

  &__el--5 {
    top: 0 !important;
    left: 90% !important;
  }

  &__el--5 span {
    width: 148px;
    height: 155px;
    background: url('../img/grain5.png') 0 0 / 100% no-repeat;
  }
}
