// colors
$bg-main: #535353;
$white: #ffffff;
$black: #000000;
$blue: #3D3BFF;
$form-white-bg: #FAFBFE;
$text-gray: #657195;
$border-gray: #E6E8F0;
$text-gray-transparent: rgba(255, 255, 255, .4);

// font-size, line-height, font-family, letter spacing
$fs-default: 16px;
$lh-default: 1;
$ff-default: "Codec Warm", sans-serif, "Arial";

// devices screen width
$mobile-width: 320px;
$tablet-width: 710px;
$tablet-max-width: 1320px;

// form box shadow
$form-shadow: 0px 2px 24px rgba(168, 175, 196, .3);
