html,
body {
  height: 100%;
}

body {
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: $ff-default;
  font-size: $fs-default;
  line-height: $lh-default;
  box-sizing: border-box;
  overflow: hidden;
}

a {
  text-decoration: none;
}

h1 {
  font-size: 72px;
  line-height: 1;
  font-weight: 700;
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%);
}

ul {
  @include reset-list;
}

.sections {
  height: 100%;
}

canvas {
	z-index: -1;
	position: absolute;
	top: 0px;
	left: 0px;
	width: 100%;
	height: 100%;
}
