.main {
  height: 100%;

  &__content {
    position: relative;
    height: calc(100% - 64px);
    display: flex;
    align-items: center;
    justify-content: space-between;
    overflow-y: hidden;
    padding: 64px 50px 0 50px;

    @include v-tablet-max {
      flex-direction: column;
      justify-content: center;
    }
  }

  &__text {
    width: 35%;
    z-index: 2;

    @include v-tablet-max {
      width: 50%;
      text-align: center;
      margin-bottom: 64px;
    }
  }

  &__title {
    line-height: 66px;

    @include v-tablet-max {
      font-size: 62px;
      line-height: 1;
    }
  }

  &__description {
    line-height: 21px;
  }

  &__image-container {
    width: 100%;
    height: 100%;
    max-width: 722px;
    max-height: 480px;
    margin: 0 auto;
    transform: rotate(-2deg);
    z-index: 2;
  }

  &__image {
    max-height: 480px;
  }

  .circle {
    position: absolute;
    bottom: -132.5px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    max-width: 265px;
    max-height: 265px;
    background-color: #FF4016;
    border-radius: 50%;
    text-align: center;
    z-index: 2;
    transition: background-color .3s;

    &:hover {
      background-color: #fff;

      & .circle__text {
        color: #000;
      }

      & .circle__arrow svg path {
        stroke: #000;
      }
    }

    &__text {
      font-size: 30px;
      line-height: 40px;
      color: #fff;
      margin: 50px 0 0 0;
      transition: color .3s;
    }

    &__arrow {
      padding: 10px;
      cursor: pointer;
    }

    &__arrow svg path {
      transition: stroke .3s;
    }
  }
}
