.header {
  position: fixed;
  display: flex;
  justify-content: space-between;
  top: 0;
  left: 0;
  width: 100%;
  padding: 64px 50px 0 50px;
  box-sizing: border-box;
  z-index: 4;

  &__menu-btn {
    position: relative;
    top: -25px;
    left: -20px;
    padding: 0;
    width: 72px;
    height: 72px;
    border: none;
    background-color: transparent;
    border-radius: 50%;
    cursor: pointer;
    transition: background-color .3s;

    &:hover {
      background-color: #C8D24E;
    }

    &:hover .hamburger .line {
      background-color: #fff;
    }
  }

  &__menu-btn.spilled {

    &:hover {
      background-color: #fff;
    }

    .hamburger .line {
      background-color: #fff;
    }

    &:hover .hamburger .line {
      background-color: #C8D24E;
    }
  }

  &__logo.spilled > svg {
    fill: #fff;
  }
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(229, 229, 229, 0.67);
  z-index: 5;
  transition: display .3s;
  display: none;

  &__active {
    display: block;
  }
}

.nav-list {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  max-width: 430px;
  height: 100%;
  padding: 0 43px 0 43px;
  background-color: #FEFCF7;
  z-index: 3;
  box-sizing: border-box;
  box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.12);
  margin-left: -100%;
  transition: margin-left .3s;
  z-index: 5;

  &__item a {
    padding: 0 0 10px 0;
    font-size: 48px;
    line-height: 78px;
    color: #000;
    transition: color .2s, border-bottom .2s;
  }

  &__item a:hover {
    cursor: pointer;
    color: #FF4016;
    border-bottom: 2px solid #FF4016;
  }

  &__active {
    margin-left: 0;
  }

  &__close {
    position: absolute;
    top: 57px;
    left: 51px;
    padding: 0;
    width: 28px;
    height: 28px;
    border: none;
    background-color: transparent;
    cursor: pointer;

    &::before,
    &::after {
      position: absolute;
      top: 46%;
      left: 0;
      content: '';
      width: 28px;
      height: 3px;
      background-color: #000;
    }

    &::before {
      transform: rotate(45deg);
    }

    &::after {
      transform: rotate(-45deg);
    }
  }
}
