// devices screen width
@mixin mobile {
  @media (min-width: #{$mobile-width}) and (max-width: #{$tablet-width - 1px}) {
    @content;
  }
}

@mixin tablet {
  @media (min-width: #{$tablet-width}) and (max-width: #{$tablet-max-width}) {
    @content;
  }
}

@mixin v-tablet-max {
  @media (max-width: #{$tablet-max-width}) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: #{$tablet-max-width + 1px}) {
    @content;
  }
}

// custom list reset
@mixin reset-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
