.order {
  position: relative;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 50px 0 50px;

  &__description {
    width: 100%;
    max-width: 300px;

    @include v-tablet-max {
      width: 50%;
      margin: 0 auto 64px auto;
      text-align: center;
    }
  }

  &__title {
    font-size: 48px;
    line-height: 52px;
    letter-spacing: -0.015em;
    margin: 50px 0 30px 0;
  }

  &__text {
    margin: 0;
  }

  &__form {
    width: 100%;
    max-width: 790px;
    min-width: 270px;
    padding: 40px 60px 48px 60px;
    margin: 0 auto;
    border-radius: 18px;
    background-color: #FCF9F0;
    background-image: url('../../img/buckwheat_bg.png');
    background-size: 189px / 245px;
    background-repeat: no-repeat;
    background-position: right 75%;
    box-sizing: border-box;
  }

  &__action-btn {
    border: none;
    background-color: #FF4016;
    color: #fff;
    cursor: pointer;
    transition: background-color .3s;

    &:hover {
      background-color: #C02300;
    }
  }

  &__inputs {
    max-width: 580px;
  }

  &__action-btn {
    width: 100%;
    max-width: 295px;
    padding: 14px 24px 14px 24px;
    font-size: 18px;
    line-height: 23px;
    border-radius: 40px;
  }

  .section1 {
    position: relative;
    height: 100%;
  }

  &__section-container {
    display: flex;
    align-items: start;
    width: 100%;

    @include v-tablet-max {
      flex-direction: column;
      align-items: center;
    }
  }

  &__footer {
    position: absolute;
    bottom: 0;
    left: 0;
    align-self: flex-end;
    width: 100%;
    height: 50px;
    background-color: #000;
    display: flex;
    align-items: center;
    justify-content: center;

    .logo-bottom {
      fill: #fff;
    }
  }
}

.range,
.multiple-range {
  display: flex;
  flex-direction: column;

  &__label {
    font-size: 16px;
    line-height: 21px;
    font-weight: 600;
  }
}

.range {
  margin-left: -15px;
  margin-bottom: 45px;

  &__single {
    position: relative;
  }

  &__slider {
    position: relative;
    z-index: 1;
    height: 8px;
    margin: 0 15px;
  }

  &__track {
    position: absolute;
    height: 3px;
    transform: translateY(-50%);
    top: 50%;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
    background-color: #B6CF70;
  }

  &__range-level {
    position: absolute;
    z-index: 2;
    left: 0;
    top: 0;
    bottom: 0;
    background-color: #C8D24E;
  }

  &__thumb {
    position: absolute;
    left: 0%;
    transform: translate(-15px, -9px);
    z-index: 3;
    width: 22px;
    height: 22px;
    background-color: #fff;
    border: 2px solid #686E29;
    border-radius: 50%;
  }

  &__info {
    position: absolute;
    top: 30px;
    left: -35px;
    width: 66px;
    white-space: nowrap;
    font-size: 12px;
    line-height: 16px;
    font-weight: 600;
    transition: all .2s;
  }

  &__input {
    position: absolute;
    top: 0;
    pointer-events: none;
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    z-index: 2;
    height: 8px;
    width: 100%;
    opacity: 0;

    &::-webkit-slider-thumb {
      pointer-events: all;
      width: 22px;
      height: 22px;
      border-radius: 0;
      border: 0 none;
      background-color: white;
      appearance: none;
      -webkit-appearance: none;
    }

    &::-moz-range-thumb {
      pointer-events: all;
      width: 22px;
      height: 22px;
      border-radius: 0;
      border: 0 none;
      background-color: white;
      appearance: none;
    }

    &::-ms-thumb {
      pointer-events: all;
      width: 22px;
      height: 22px;
      border-radius: 0;
      border: 0 none;
      background-color: white;
      appearance: none;
    }

    // &:active::-ms-thumb {
    //   pointer-events: all;
    //   width: 22px;
    //   height: 22px;
    //   border-radius: 0;
    //   border: 0 none;
    //   background-color: white;
    //   appearance: none;
    // }
  }

  &__label {
    padding-left: 15px;
    margin-bottom: 20px;
  }
}

.multiple-range {
  margin-left: -15px;
  margin-bottom: 50px;

  &__multi-box {
    position: relative;
  }

  &__slider {
    position: relative;
    z-index: 1;
    height: 8px;
    margin: 0 15px;
  }

  &__track {
    position: absolute;
    height: 3px;
    transform: translateY(-50%);
    top: 50%;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
    background-color: #B6CF70;
  }

  &__range {
    position: absolute;
    z-index: 2;
    left: 25%;
    right: 25%;
    top: 0;
    bottom: 0;
    background-color: #C8D24E;
  }

  &__thumb {
    position: absolute;
    z-index: 3;
    width: 22px;
    height: 22px;
    background-color: #fff;
    border: 2px solid #686E29;
    border-radius: 50%;
    // opacity: 0;
  }

  &__thumb.left {
    left: 25%;
    transform: translate(-15px, -9px);
  }

  &__thumb.right {
    right: 25%;
    transform: translate(15px, -9px);
  }

  &__input {
    position: absolute;
    top: 0;
    pointer-events: none;
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    z-index: 2;
    height: 8px;
    width: 100%;
    opacity: 0;

    &::-webkit-slider-thumb {
      pointer-events: all;
      width: 22px;
      height: 22px;
      border-radius: 0;
      border: 0 none;
      background-color: white;
      appearance: none;
      -webkit-appearance: none;
    }

    &::-moz-range-thumb {
      pointer-events: all;
      width: 22px;
      height: 22px;
      border-radius: 0;
      border: 0 none;
      background-color: white;
      appearance: none;
    }

    &::-ms-thumb {
      pointer-events: all;
      width: 22px;
      height: 22px;
      border-radius: 0;
      border: 0 none;
      background-color: white;
      appearance: none;
    }
  }

  &__label {
    padding-left: 15px;
    margin-bottom: 20px;
  }

  &__val {
    position: absolute;
    display: flex;
    top: 30px;
    left: 64px;
  }

  &__value {
    font-size: 12px;
    line-height: 16px;
    font-weight: 600;
    color: #0C0C0C;

    &--dash {
      margin: 0 4px;
    }

    &--minutes {
      margin-left: 4px;
    }
  }
}

.radio {

  &__label {
    font-size: 16px;
    line-height: 21px;
    font-weight: 600;
  }

  &__collection {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 32px;
  }
}

.radio-collection {

  &__clause {
    position: relative;
    margin: 20px 8px 0 0;
  }

  &__label {
    margin-left: 31px;
    white-space: nowrap;
  }

  &__input + label::before {
    position: absolute;
    top: -2px;
    left: 0;
    content: '';
    width: 18px;
    height: 18px;
    background: #fff;
    border: 1px solid #686E29;
    border-radius: 6px;
  }

  &__input:checked + label::after {
    position: absolute;
    top: 4px;
    left: 6px;
    content: '';
    width: 8px;
    height: 8px;
    border-radius: 2px;
    background: #C8D24E;
  }
}

.contacts {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  max-width: 475px;

  &__box {
    margin-bottom: 32px;
  }

  &__box--name {
    display: flex;
    flex-direction: column;
  }

  &__box--phone {
    display: flex;
    flex-direction: column;
  }

  &__label {
    font-size: 16px;
    line-height: 21px;
    font-weight: 600;
    margin-bottom: 10px;
  }

  &__input {
    width: 100%;
    padding: 11px 0 10px 15px;
    background: #fff;
    border: 1px solid #BBBBBB;
    border-radius: 6px;
    height: 40px;
    max-width: 222px;
    box-sizing: border-box;
    font-size: 16px;
    line-height: 21px;
    font-family: $ff-default;

    &::placeholder {
      font-size: 16px;
      line-height: 21px;
      font-family: $ff-default;
      color: #7C7C7C;
    }
  }
}
